.flo-checkbox {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: row-reverse;
  input[type=checkbox] {
    display: none;
    &:checked {
      & + span {
        i {
          display: block;
        }
      }
    }
  }
  input[type=radio] {
    display: none;
    &:checked {
      & + span {
        i {
          display: block;
        }
      }
    }
  }
  span {
    @include size(20px);
    border: 1px solid #000;
    margin-right: 5px;
    position: relative;
    i {
      display: none;
      position: absolute;
      color: $orange;
      left: 2px;
      top: 2px;
      font-size: 14px;
    }
  }
  &.active {
    color: $orange;
    &.color-inherit {
      color: inherit;
    }
  }
  &.with-links {
    display: block;
    span {
      float: left;
      position: relative;
      top: 2px;
    }
    a {
      color: #000;
      text-decoration: underline;
    }
  }
}