.error-page-wrapper {
  background-color: #fff;
  .logo {
    margin-bottom: 30px;
    display: inline-block;
    img {
      width: 106px;
      height: auto;
    }
  }
  h1 {
    color: #7c7c7c;
    font-size: 25px;
    font-weight: 500;
    margin-bottom: 20px;
  }
  p {
    color: #7c7c7c;
    font-weight: 500;
    margin-bottom: 20px;
    font-size: 15px;
  }
  .search-form {
    display: flex;
    align-items: center;
    justify-content: center;
    .form-control {
      height: 40px;
    }
    button {
      background-color: $orange;
      @include size(43px, 40px);
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      left: -2px;
    }
  }
  .category-box {
    img {
      margin-bottom: 9px;
      width: 100%;
      height: auto;
    }
    h4 {
      color: #7c7c7c;
      text-align: center;
      margin: 0;
      font-size: 12px;
      font-weight: normal;
    }
  }
  @media (max-width: $ipadportrait) {
    .page-animation {
      img {
        width: 100%;
      }
    }
  }
}