.breadcrumbs {
  background-color: #fff;
  margin: 10px 0;
  @include list-unstyled;
  padding: 6px 9px;
  display: flex;
  align-items: center;
  font-size: 14px;
  color: #898989;
  overflow: auto;
  li {
    position: relative;
    white-space: nowrap;
    &:not(:last-child) {
      margin-right: 24px;
      &:after {
        content: ">";
        position: absolute;
        right: -14px;
        top: 50%;
        margin-top: -12px;
      }
    }
    a {
      color: #898989;
      display: flex;
      align-items: center;
    }
    &:last-child {
      padding-right: 9px;
    }
    &.go-back-link {
      position: absolute;
      background-color: #fff;
      height: 33px;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 45px;
      left: 0;
      &:after {
        content: "";
      }
    }
  }
  &.with-go-back {
    margin-left: 45px;
    margin-bottom: 5px;
  }
}