.btn {
  &.outline-gray {
    border-radius: 8px;
    border: 1px solid #a0a0a0;
    padding: 8px 12px;
    color: #000;
    font-size: 14px;
    text-transform: uppercase;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;

    i {
      margin-left: 14px;
    }

    &:hover {
      background-color: #f3f3f3;
    }
  }

  &.orange {
    border-radius: 8px;
    border: 1px solid $orange;
    background-color: $orange;
    padding: 8px 10px;
    color: #fff;
    font-size: 14px;
    text-transform: uppercase;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;

    i {
      margin-left: 14px;
    }
  }

  &.orange-gradient {
    width: 100%;
    border: 2px solid $orange;
    background: rgb(255, 204, 0);
    background: linear-gradient(180deg, rgba(255, 204, 0, 1) 0%, rgba(240, 215, 0, 1) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #000;

    i {
      margin-left: 9px;
      font-size: 100%;
    }

    &.btn-lg {
      height: 60px;
    }
  }
}

section.our-choices {
  background-color: #FFFAFA;
  padding: 9px 0;

  h3 {
    font-weight: 300;
    font-size: 24px;
    text-align: center;
    margin-bottom: 10px;
  }

  .product-slider {
    white-space: nowrap;
    overflow: auto;
    display: flex;

    .product-box {
      width: 170px;
      height: 285px;
      float: left;

      &:not(:last-child) {
        margin-right: 9px;
      }
    }
  }
}

.fs-14 {
  font-size: 14px;
}

.fs-15 {
  font-size: 15px;
}

.text-dark {
  color: #000 !important;
}

.text-light-gray {
  color: #a2a2a2;
}

.table {
  th {
    font-weight: normal;
    padding: 5px;
  }
  td {
    vertical-align: middle;
    padding: 8px;
  }
}

.custom-select {
  line-height: 1.2;
}