.product-detail-page-wrapper {
  font-weight: 300;
  .title-box {
    padding: 8px 9px;
    background-color: #fff;
    margin-bottom: 10px;
    h1 {
      font-weight: 300;
      font-size: 14px;
      color: $black;
      margin: 0;
    }
    .description {
      font-size: 14px;
      color: #484848;
    }
  }
  .product-detail-slider {
    position: relative;
    background-color: #fff;
    margin-bottom: 9px;
    .arrow {
      @include size(27px);
      position: absolute;
      top: 50%;
      margin-top: -13.5px;
      &.prev {
        background: url("../img/icons/slide-arrow-left.svg") no-repeat center center;
        left: 5px;
        &:hover {
          background: url("../img/icons/slide-arrow-left-orange.svg") no-repeat center center;
        }
      }
      &.next {
        background: url("../img/icons/slide-arrow-right.svg") no-repeat center center;
        right: 5px;
        &:hover {
          background: url("../img/icons/slide-arrow-right-orange.svg") no-repeat center center;
        }
      }
    }
    .add-to-favourites-button {
      position: absolute;
      right: 0;
      top: 0;
      padding: 10px;
    }
    .prices {
      font-size: 16px;
      margin-bottom: 5px;
      text-align: center;
      span {
        color: #666666;
        text-decoration: line-through;
        margin-right: 5px;
      }
    }
    .discount {
      font-size: 18px;
      padding: 4px 5px;
      border: 1px dashed #959595;
      display: table;
      margin: auto;
      .text-warning {
        font-weight: 500;
        margin-left: 5px;
      }
    }
  }
  .choices-box {
    padding: 9px;
    margin-bottom: 9px;
    background-color: #fff;
    display: inline-block;
    width: 100%;
    .color-selection {
      width: 100%;
      white-space: nowrap;
      overflow: auto;
    }
    .form-check {
      display: inline-block;
      padding: 0;
      margin-right: 4px;
      cursor: pointer;
      input {
        display: none;
        & + label {
          border: solid 1px #fff;
          cursor: pointer;
        }
        &:checked + label {
          border: solid 1px #d2d2d2;
        }
      }
    }
    .size-selection {
      display: inline-block;
      width: 100%;
      .title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 18px;
        margin-bottom: 10px;
        a {
          color: $black;
          text-decoration: underline;
          font-size: 15px;
        }
      }
      .form-check {
        margin-right: 4px;
        float: left;
        input {
          &:checked + label {
            background-color: $orange;
            color: #fff;
          }
        }
        label {
          @include size(44.4px);
          line-height: 45px;
          text-align: center;
          font-size: 15px;
          border: solid 1px #d2d2d2;
          margin-bottom: 4px;
        }
        &.disabled {
          input + label {
            color: #d2d2d2;
            cursor: pointer;
          }
          input:checked + label {
            color: #fff;
          }
        }
      }
    }
  }
  .add-to-cart-button, .get-informed-instock-button {
    height: 48px;
    width: 100%;
    border: 2px solid $orange;
    font-size: 20px;
    background: rgb(255,204,0);
    background: linear-gradient(180deg, rgba(255,204,0,1) 0%, rgba(240,215,0,1) 100%);
    display: flex;
    align-items: center;
    justify-content: center;
    i {
      margin-left: 9px;
      font-size: 22px;
    }
  }
  .get-informed-instock-button {
    display: none;
  }
  .shipping-info {
    font-size: 12px;
    color: #666666;
    margin: 10px 0;
    text-align: center;
  }
  .icons {
    display: flex;
    align-items: center;
    .icon-box {
      border: 1px solid #a0a0a0;
      padding: 5px 4px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: calc(50% - 4px);
      height: 40px;
      border-radius: 5px;
      .title {
        font-size: 11px;
        line-height: 16px;
        font-weight: 400;
      }
      figure {
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }
  .product-infos {
    background-color: #fff;
    padding: 8px;
    margin: 9px 0;
    .holder {
      table {
        font-size: 12px;
        color: #919191;
        font-weight: 300;
        width: 100%;
        tr {
          td {
            padding: 3px 0;
            span {
              color: $black;
            }
            &:first-child {
              width: 34%;
            }
          }
        }
      }
    }
  }
  .discount-alert {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 9px;
    .get-informed-button {
      background-color: #fff;
      height: 27px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: none;
      width: calc(50% - 5px);
      font-size: 12px;
      color: #666666;
      text-transform: uppercase;
      font-weight: 300;
      i {
        margin-right: 10px;
      }
    }
    .seller-info {
      background-color: #fff;
      width: calc(50% - 5px);
      height: 27px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 12px;
      font-weight: normal;
      a {
        color: #666666;
      }
    }
  }
}