.search-panel {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #fafafa;
  z-index: 9999;
  visibility: hidden;
  opacity: 0;
  transition: all ease .3s;
  transform: translateY(100px);
  .panel-header {
    padding: 23px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0,0,0,.1);
    background-color: #fafafa;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 99;
    .close-search-panel {
      display: flex;
      align-items: center;
    }
    .search-form {
      position: relative;
      width: 90%;
      .form-control {
        line-height: 42px;
        height: 42px;
        border-color: $orange;
        font-weight: normal;
        font-size: 10px;
        font-style: italic;
        &:focus {
          font-size: 12px;
        }
      }
      button {
        position: absolute;
        right: 3px;
        top: 10px;
        background: transparent;
        border: none;
        display: flex;
        align-items: center;
      }
    }
  }
  .panel-body {
    .latest-search {
      padding: 10px 20px;
      border-bottom: 1px solid rgba(0,0,0,.1);
      margin-top: 90px;
      .title-bar {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 15px;
        h5 {
          font-weight: normal;
          color: #adadad;
          font-style: italic;
          font-size: 12px;
          margin-bottom: 0;
        }
        .clear-search {
          color: #adadad;
          display: flex;
          align-items: center;
          font-weight: normal;
          font-style: italic;
          font-size: 12px;
          text-decoration: underline;
          i {
            margin-right: 4px;
          }
        }
      }
      .search-list {
        @include list-unstyled;
        margin: 0;
        li {
          a {
            font-weight: normal;
            color: #000;
            font-style: italic;
            display: block;
            margin-bottom: 10px;
          }
        }
      }
    }
    .search-results {
      background-color: #fff;
      margin: 100px 10px;
      overflow-y: scroll;
      max-height: calc(100vh - 160px);
      display: none;
      .product {
        padding: 10px 20px 10px 10px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        img {
          width: 70px;
          min-width: 70px;
          border: 1px solid #f8f8f8;
          margin-right: 20px;
        }
        .info-holder {
          max-width: 120px;
          .title {
            display: block;
            font-weight: bold;
            color: #000;
            margin-bottom: 3px;
          }
          .description {
            font-size: 10px;
            line-height: 12px;
            display: block;
            color: #000;
            font-style: italic;
          }
        }
        .price-holder {
          display: flex;
          flex-direction: column;
          font-size: 18px;
          line-height: 21px;
          .old-price {
            color: #666666;
            text-decoration: line-through;
          }
          .price {
            font-weight: normal;
          }
        }
        &:not(:last-child) {
          border-bottom: 1px solid #f8f8f8;
        }
      }
    }
    .show-all {
      position: fixed;
      z-index: 99;
      bottom: 0;
      right: 0;
      left: 0;
      font-size: 15px;
      padding: 17px;
      font-weight: normal;
      font-style: italic;
      display: none;
    }
  }
  &.active {
    visibility: visible;
    opacity: 1;
    transform: translateY(0px);
  }
}