.site-header {
  position: relative;
  z-index: 12;
  background-color: #fff;
  .main-menu {
    padding: 40px 9px 15px;
    .top-menu {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 10px 9px;
      background-color: #fff;
      z-index: 9999;
    }
    .menu-left {
      display: flex;
      align-items: center;
      .open-menu-button {
        display: flex;
        align-items: center;
        margin-right: 10px;
      }
      .logo {
        img {
          width: 60px;
        }
      }
    }
    .menu-right {
      .nav-top {
        @include list-unstyled;
        margin: 5px 0 0 0;
        display: flex;
        align-items: center;
        li {
          padding: 0 5px;
          a {
            position: relative;
            display: flex;
            align-items: center;
            justify-content: center;
          }
          &:last-child {
            padding-right: 0;
          }
          &.mini-cart {
            a {
              display: flex;
              align-items: center;
              .cart-count {
                position: relative;
                .number {
                  font-size: 12px;
                  font-weight: 500;
                  color: $black;
                  position: absolute;
                  left: 6px;
                  bottom: 5.5px;
                }
              }
            }
          }
        }
      }
    }
    .search-form {
      position: relative;
      padding: 15px 0;
      .form-control {
        border-color: #bfbfbf;
        border-radius: 0;
        width: 100%;
        font-style: italic;
        font-size: 12px;
        height: auto;
        line-height: 30px;
        color: #9f9f9f;
        padding: 0 10px;
        &:focus {
          border-color: $orange;
        }
      }
      button {
        background: none;
        border: none;
        position: absolute;
        right: 7px;
        padding: 0;
        top: 22px;
      }
    }
    .bottom-menu {
      position: relative;
      & > ul {
        @include list-unstyled;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        white-space: nowrap;
        overflow-x: auto;
        & > li {
          padding: 0 15px;
          & > a {
            font-size: 16px;
            font-weight: 300;
            color: $black;
            position: relative;
            &:hover {
              color: $orange;
            }
          }
          &:first-child {
            padding-left: 0;
          }
          &:last-child {
            //padding-right: 0;
          }
          &:not(:last-child) {
            & > a:after {
              content: " ";
              position: absolute;
              @include size(4px);
              border-radius: 50%;
              background-color: #d0d0d0;
              right: -15px;
              top: 50%;
              border: none;
            }
          }
        }
      }
      &:after {
        content: " ";
        position: absolute;
        @include size(24px);
        background: rgb(255,255,255);
        background: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
        right: 0;
        top: 0;
      }
    }
  }
  &.header-payment {
    padding: 12px;
    text-align: center;
    .logo img {
      width: 60px;
    }
  }
}