.product-box {
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-direction: column;
  margin-bottom: 9px;
  figure {
    position: relative;
    margin-bottom: 5px;
    width: 100%;
    .badge {
      color: #fff;
      font-size: 12px;
      border-radius: 0;
      font-weight: 300;
      text-transform: uppercase;
      position: absolute;
      left: 3px;
      top: 3px;
      width: 80px;
      &.badge-info {
        background-color: #003f67;
      }
      &.badge-dark {
        background-color: rgb(58, 0, 65);
      }
      &.badge-danger {
        background-color: rgb(120, 0, 0);
      }
      &.badge-success {
        background-color: rgb(12, 103, 0);
      }
    }
    .add-to-favourites-button {
      position: absolute;
      right: 3px;
      top: 3px;
    }
  }
  img {
    width: 100%;
    height: auto;
  }
  .title {
    font-size: 16.5px;
    color: #000;
    text-align: center;
    font-weight: 500;
    margin-bottom: 3px;
    white-space: normal;
  }
  .description {
    color: #909090;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 17px;
    font-size: 12px;
    height: 18px;
    padding: 0 15px;
    text-align: center;
    width: 100%;
  }
  .prices {
    font-weight: 300;
    margin-top: 20px;
    width: 100%;
    text-align: center;
    margin-bottom: 21px;
    .old-price {
      color: #a5a5a5;
      font-size: 14px;
      text-decoration: line-through;
    }
    .price {
      font-size: 15px;
      font-weight: 500;
    }
    &.discounted {
      margin: 5px 0 7px;
      border-bottom: 1px solid #efefef;
      padding-bottom: 5px;
      .old-price {
        font-size: 15px;
      }
      .price {
        font-size: 15px;
        color: $black;
      }
    }
  }
  .discount {
    color: $black;
    text-align: center;
    font-size: 15px;
    line-height: 16px;
    padding-bottom: 7px;
    .text-warning {
      font-weight: 500;
    }
  }
  &:focus {
    outline: none;
  }
}