.login-page-wrapper {
  .form-holder {
    padding: 25px;
    background-color: #fff;
    .link {
      color: #969696;
      text-decoration: underline;
      font-weight: normal;
    }
  }
  .btn-lg {
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    padding: 11px;
    .fa-angle-right {
      font-size: 22px;
      margin-left: 8px;
    }
    &.btn-info {
      background-color: #48629b;
      border-color: #48629b;
      position: relative;
      .fa-facebook-f {
        position: absolute;
        left: 18px;
        top: 11px;
        font-size: 26px;
      }
    }
  }
  .gender-choice {
    display: flex;
    align-items: center;
    .title {
      color: #827d7d;
      font-size: 12px;
      font-style: italic;
      margin-right: 20px;
    }
    label {
      margin-bottom: 0;
      margin-right: 20px;
      color: #827d7d;
      text-transform: none;
      input:checked {
        & + i + span {
          color: #827d7d;
        }
      }
    }
  }
  .checkboxes {
    label {
      display: block;
      color: #827d7d;
      font-size: 12px;
      margin-bottom: 20px;
      span {
        min-width: 20px;
        border-width: 2px;
        float: left;
        i {
          top: 0;
        }
      }
      input {
        display: block;
        visibility: hidden;
        position: absolute;
      }
      input:checked + span {
        color: inherit;
      }
      .text-holder {
        padding-left: 30px;
        a {
          color: #000;
          text-decoration: underline;
        }
      }
      & + .error {
        color: #ff0000;
        font-size: 12px;
        padding-left: 30px;
        margin-top: -15px;
        display: block;
        margin-bottom: 15px;
      }
    }
  }
  .password-reset-alert {
    background-color: #fff;
    text-align: center;
    padding: 25px;
    .fa-check {
      color: $green;
      font-size: 50px;
    }
    p {
      font-size: 15px;
      font-weight: normal;
      color: #a2a2a2;
    }
  }
}