.footer-content {
  padding: 10px 20px 15px;
  background-color: #fff;
  margin-top: 9px;
  .holder {
    font-size: 14px;
    line-height: 15px;
    font-weight: 300;
    color: #949494;
    height: 47px;
    overflow-y: hidden;
    margin-bottom: 15px;
    text-align: center;
  }
  .read-more {
    display: flex;
    align-items: center;
    flex-direction: column;
    font-size: 14px;
    font-weight: 300;
    color: $black;
    .svg-arrow-up {
      display: none;
    }
  }
  &.expanded {
    .holder {
      height: auto;
    }
    .read-more {
      .svg-arrow-up {
        display: block;
      }
      .svg-arrow-down {
        display: none;
      }
    }
  }
}

footer.site-footer {
  background-color: #f5f5f5;
  .footer-top {
    padding: 20px 0 0;
    position: relative;
    .widgets-holder {
      display: flex;
      align-items: flex-start;
      justify-content: space-between;
      .footer-widget {
        &:not(:last-child) {
          margin-right: 30px;
        }
        h5 {
          font-size: 12px;
          font-weight: normal;
          margin-bottom: 15px;
        }
        ul.footer-links {
          @include list-unstyled;
          margin-bottom: 30px;
          li {
            margin-bottom: 15px;
            a {
              display: block;
              font-weight: 300;
              color: $black;
            }
          }
        }
      }
    }
    .app-links {
      position: absolute;
      right: 0;
      bottom: 15px;
    }
  }
  .footer-bottom {
    margin-bottom: 15px;
  }
  .copyright {
    background-color: $black;
    color: #fff;
    font-size: 12px;
    text-align: center;
    padding: 5px 0;
  }
}