.cart-modal {
  .modal-header {
    padding: 10px;
    align-items: center;
    justify-content: center;
    background-color: #f1f1f1;
    border-bottom: none;
    h5 {
      font-size: 18px;
      font-weight: 500;
      text-align: center;
      color: #000;
      text-shadow: none;
    }
    .close {
      right: 15px;
      top: 10px;
      position: absolute;
    }
  }
  .modal-body {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 18px;
    color: #000;
    .total-count {
      position: relative;
      margin-bottom: 10px;
      span {
        position: absolute;
        left: 0;
        right: 0;
        text-align: center;
        bottom: 11px;
        font-size: 25px;
        font-weight: 500;
        color: $orange;
      }
    }
    h4 {
      font-size: 16px;
      font-weight: normal;
      font-style: italic;
    }
    p {
      font-size: 12px;
      font-weight: normal;
      font-style: italic;
      margin-bottom: 0;
    }
  }
  .modal-footer {
    border-top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0;
    .btn {
      width: 50%;
      margin: 0 !important;
      padding: 10px;
      font-size: 14px;
      &.btn-secondary {
        background-color: #E5E5E5;
        border-color: #E5E5E5;
        color: #000;
      }
    }
  }
}