.campaign-box {
  border: 1px solid #dbdcdb;
  background-color: #fff;
  margin-bottom: 15px;
  .text-holder {
    padding: 15px 15px 15px 0;
    h5 {
      font-size: 16px;
      font-weight: normal;
      color: $orange;
    }
    p {
      font-weight: 300;
      font-size: 14px;
      margin-bottom: 10px;
      max-height: 88px;
      overflow-y: auto;
      padding-right: 5px;
      &::-webkit-scrollbar {
        width: 5px;
      }

      &::-webkit-scrollbar-track {
        background: #f1f1f1;
      }

      &::-webkit-scrollbar-thumb {
        background: #b7b7b7;
      }
    }
    .date {
      font-size: 14px;
      margin-bottom: 15px;
    }
  }
  @media (max-width: $ipadlandscape) {
    .text-holder {
      text-align: center;
      p {
        max-height: initial;
      }
    }
  }
}