.brands-page-wrapper {
  .search-form-wrapper {
    margin: 6px 0;
    padding: 15px 9px;
    background: url("../img/brands-bg.jpg") repeat;
    h2 {
      text-align: center;
      font-size: 18px;
      margin-bottom: 15px;
      font-weight: normal;
    }
    .search-form {
      max-width: 624px;
      margin: auto;
      position: relative;
      .form-control {
        border-color: #dbdcdb;
        height: 40px;
        &:focus {
          border-color: $orange;
        }
      }
      button {
        position: absolute;
        width: 40px;
        height: 40px;
        background-color: $orange;
        border: none;
        right: 0;
        top: 0;
        z-index: 2;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
  .brand-boxes {
    background-color: #fff;
    padding: 9px 9px 0;
    margin-bottom: 9px;
  }
  .brand-box {
    margin-bottom: 9px;
    img {
      width: 100%;
      height: auto;
    }
  }
  .brand-list-holder {
    background-color: #fff;
    position: relative;
    &:after {
      content: " ";
      position: absolute;
      width: 34px;
      height: 40px;
      background: #fff;
      background: linear-gradient(90deg,hsla(0,0%,100%,0),#fff);
      right: 0;
      top: 0;
    }
    .alphabetic-list {
      @include list-unstyled;
      margin: 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 3px 7px 10px 7px;
      white-space: nowrap;
      overflow: auto;
      position: relative;
      li {
        margin-right: 15px;
        a {
          font-size: 25px;
          font-weight: normal;
          color: $body-color;
          &.active {
            color: $orange;
          }
        }
        &:not(:last-child) {
          a {
            position: relative;
            &:after {
              content: ".";
              color: #c4c4c4;
              position: absolute;
              right: -10px;
              bottom: 0;
            }
          }
        }
      }
    }
  }
  .single-letter-brand {
    .letter {
      width: 100%;
      font-size: 28px;
      font-weight: normal;
      color: #fff;
      background-color: #bfbfbf;
      padding-left: 18px;
    }
    ul {
      @include list-unstyled;
      margin: 0;
      display: inline-block;
      width: 100%;
      padding: 12px 12px 12px 18px;
      li {
        float: left;
        width: 33.33333%;
        a {
          color: $body-color;
          font-size: 14px;
          font-weight: 300;
        }
      }
    }
  }
  .show-all-brands {
    text-align: center;
    padding: 15px;
    display: block;
    font-weight: 300;
  }
}