.static-page-wrapper {
  margin: 0 -9px;
  .static-page-slider {
    .slick-dots {
      bottom: 20px;
      li {
        @include size(16px);
        margin: 0 4px;
        button {
          border: none;
          background-color: #000;
          opacity: .4;
        }
        &.slick-active {
          button {
            opacity: 1;
          }
        }
      }
    }
  }
  .animated-image-wrapper {
    overflow: hidden;
    margin-bottom: 0;
    display: inline-block;
    width: 100%;
    img {
      transition: all ease .3s;
      width: 100%;
      height: auto;
    }
    &:hover {
      img {
        transform: scale(1.04);
        
      }
    }
  }
  .chance-box {
    border: 1px solid $gray-200;
    display: inline-block;
    width: 100%;
    color: $black;
    background-color: #fff;
    overflow: hidden;
    h4 {
      color: #fff;
      background-color: #592E7B;
      text-align: center;
      font-size: 16px;
      padding: 5px;
    }
    .product {
      padding: 10px 10px 0 10px;
      text-align: center;
      .title {
        font-size: 14px;
        text-align: center;
        line-height: 16px;
        height: 36px;
      }
      .prices {
        text-align: center;
        font-size: 15px;
        .current {
          color: #592E7B;
        }
        .discount {
          text-decoration: line-through;
          font-weight: 300;
        }
      }
      img {
        transition: all ease .3s;
      }
    }
    &:hover {
      border-color: #592E7B;
      img {
        transform: scale(1.04);
      }
    }
  }
  .animated-category-box {
    display: inline-block;
    width: 100%;
    position: relative;
    img {
      width: 100%;
      height: auto;
      transition: all ease .3s;
    }
    .category {
      position: absolute;
      background-color: #fff;
      color: $black;
      width: 140px;
      height: 34px;
      text-align: center;
      text-transform: uppercase;
      font-weight: 300;
      font-size: 18px;
      line-height: 34px;
      left: 50%;
      margin-left: -70px;
      top: 50%;
      margin-top: -17px;
      z-index: 9;
    }
    .action {
      transition: all ease .3s;
      position: absolute;
      color: $black;
      width: 100%;
      height: 34px;
      text-align: center;
      text-transform: uppercase;
      font-weight: 300;
      font-size: 18px;
      left: 0;
      right: 0;
      top: 30px;
      z-index: 9;
      display: flex;
      align-items: center;
      justify-content: center;
      opacity: 0;
      transform: translateY(-10px);
      span {
        background-color: #fff;
        line-height: 34px;
        padding: 0 10px;
      }
    }
    &:hover {
      img {
        transform: scale(1.04);
        -webkit-box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.19);
        -moz-box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.19);
        box-shadow: 0px 0px 8px 0px rgba(0,0,0,0.19);
      }
      .action {
        opacity: 1;
        transform: translateY(0);
      }
    }
  }
  .featured-model {
    color: #000;
    img.shadow {
      box-shadow: 0 0 65px rgba(0,0,0,0.15);
    }
    .holder {
      max-width: 90%;
      background-color: #fff;
      display: block;
      margin: auto;
      box-shadow: 0 0 65px 0 rgba(0,0,0,0.15);
      padding: 50px 50px 100px;
      .new-model-button {
        background-color: #000;
        color: #fff;
        display: inline-block;
        margin: 0 auto 30px auto;
        padding: 5px 20px;
      }
      .title {
        text-align: center;
        font-size: 72px;
        font-weight: bold;
      }
    }
    .infos-holder {
      box-shadow: 0 0 65px 0 rgba(0,0,0,0.15);
      background-color: #454545;
      padding: 0 70px;
      position: relative;
      top: -80px;
      .row {
        position: relative;
        top: -70px;
        margin-top: 80px;
        .text-holder {
          padding: 30% 70px;
          background-color: #fff;
          box-shadow: 0 0 65px 0 rgba(0,0,0,0.15);
          position: relative;
          h4 {
            font-size: 30px;
          }
          p {
            font-size: 16px;
            margin-bottom: 0;
          }
          .gallery {
            text-align: center;
            display: flex;
            align-items: center;
            position: absolute;
            left: -30px;
            bottom: -30px;
            a {
              margin: 0 5px;
              display: inline-block;
              border: 1px solid $gray-300;
              width: 120px;
            }
            img {
              height: auto;
              width: 100%;
            }
          }
        }
      }
    }
  }
  .text-collapse {
    color: #6c6c6c;
    max-width: 943px;
    box-shadow: 0 0 65px rgba(0,0,0,0.15);
    background-color: #fff;
    margin: 0 auto;
    padding: 30px;
    position: relative;
    height: 185px;
    overflow: hidden;
    .collapser {
      font-size: 28px;
      color: #000;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      text-align: center;
      background-color: #fff;
    }
    &.active {
      height: auto;
      .collapser {
        transform: rotate(180deg);
      }
    }
  }
  .video-container {
    position: relative;
    margin-bottom: 40px;
    video {
      width: 100%;
      height: auto;
    }
    .overlay-desc {
      background: rgba(0,0,0,0);
      position: absolute;
      right: 0;
      left: 0;
      bottom: 0;
      text-align: center;
      top: 0;
      cursor: pointer;
      z-index: 9;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &.active {
      .overlay-desc {
        img {
          opacity: 0;
        }
      }
    }
  }
  @media only screen and (max-width: 668px) {
    .animated-image-wrapper, .chance-box {
      margin-bottom: 20px;
    }
    .animated-category-box {
      margin-bottom: 10px;
    }
    .featured-model {
      .holder {
        padding: 30px 30px 100px;
        .title {
          font-size: 32px;
        }
        img {
          width: 100%;
          margin-bottom: 20px;
        }
      }
      .infos-holder {
        padding: 0 30px;
        .row {
          .text-holder {
            padding: 20px;
            .gallery {
              position: initial;
              margin: 10px 0;
              flex-direction: column;
              a {
                margin: 0 0 10px 0;
                width: 100%;
              }
            }
          }
        }
      }
    }
  }
}