.page-title-bar {
  background-color: #fff;
  margin: 5px 0;
  padding: 7px 9px;
  display: flex;
  align-items: center;
  .go-back-link {
    display: flex;
    align-items: center;
    margin-right: 16px;
  }
  h1,h2,h3,h4,h5,h6 {
    font-size: 14px;
    color: $black;
    font-weight: 300;
    margin: 0;
    text-transform: uppercase;
  }
}