.main-slider {
  padding-bottom: 30px;
  .slide {
    .image-holder {
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      max-width: 100%;
      img {
        width: 100%;
      }
    }
  }
  .slick-dots {
    width: 100%;
    left: 0;
    margin-left: auto;
    right: 0;
    text-align: center;
    bottom: 9px;
    li {
      @include size(13px);
      button {
        @include size(13px);
        background-color: #fff;
        border-color: #cbcbca;
      }
      &.slick-active {
        button {
          background-color: $orange;
          border-color: $orange;
        }
      }
    }
  }
}