.account-page-wrapper {
  .account-menu {
    @include list-unstyled;
    margin-bottom: 0;
    background-color: #fff;
    padding: 0 10px;
    li {
      display: flex;
      align-items: center;
      a {
        display: flex;
        align-items: center;
        color: $black;
        font-size: 18px;
        padding: 0 20px;
        height: 55px;
        width: 100%;
        i {
          margin-right: 40px;
        }
        &:hover {
          color: $orange;
        }
      }
      &:not(:last-child) {
        border-bottom: 1px solid #e5e5e5;
      }
    }
  }
  h2 {
    font-size: 16px;
    font-weight: normal;
    margin-bottom: 10px;
  }
  label {
    font-weight: normal;
    color: #000;
    margin-bottom: 3px;
    &.error {
      color: red;
      font-size: 12px;
    }
    &.flo-radio {
      color: #000;
    }
  }
  select {
    padding-left: 5px;
    padding-right: 5px;
  }
  span.error.help-block {
    color: red;
  }
  .flo-checkbox {
    font-size: 12px;
    align-items: flex-start;
    span {
      min-width: 20px;
    }
  }
  .card-list {
    display: inline-block;
    width: 100%;
    .card-holder {
      margin-right: 12px;
      margin-bottom: 15px;
      .credit-card {
        background-color: #ebebeb;
        padding: 15px;
        color: #000;
        font-size: 14px;
        font-weight: 300;
        position: relative;
        margin-bottom: 12px;
        h5 {
          font-size: 14px;
          margin-bottom: 30px;
        }
        img {
          position: absolute;
          right: 15px;
          top: 15px;
        }
      }
      a {
        font-size: 14px;
        color: #000;
        text-transform: uppercase;
        text-decoration: underline;
        margin-right: 17px;

      }
    }
    .add-new-card {
      border: 1px solid #dbdcdb;
      width: 100%;
      height: 118px;
      display: inline-flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      float: left;
      font-size: 14px;
      color: #000;
      text-transform: uppercase;
      i {
        margin-bottom: 12px;
      }
      &:hover {
        background-color: #f6f6f6;
      }
    }
  }
  .order-box {
    background-color: #fff;
    padding: 8px;
    width: 100%;
    color: #000;
    font-size: 12px;
    margin-bottom: 9px;
    .btn {
      padding: 3px 10px;
      border-radius: 8px;
      font-size: 12px;
    }
    .infos-holder {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #d0d0d0;
      padding-bottom: 9px;
      margin-bottom: 9px;
      font-size: 12px;
    }
    .status-holder {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .status-date {
        .status {
          display: inline-flex;
          align-items: center;
          white-space: nowrap;
          .svg-order-shipping {
            min-width: 38px;
          }
        }
      }
      .products {
        @include list-unstyled;
        margin: 0;
        display: flex;
        align-items: center;
        li {
          a {
            img {
              width: 62px;
              height: auto;
              border: 1px solid #f2f2f2;
            }
          }
          &:not(:last-child) {
            margin-right: 10px;
          }
        }
      }
    }
  }
  .order-detail-box {
    background-color: #fff;
    padding: 9px;
    width: 100%;
    color: #000;
    font-size: 14px;
    margin-bottom: 15px;
    .detailed-infos {
      margin-bottom: 12px;
      font-size: 12px;
    }
    .order-status {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 12px 0;
      border-top: 1px solid #eeeeee;
      .status-box {
        display: flex;
        align-items: center;
        position: relative;
        figure {
          margin: 0;
          @include size(50px);
          border-radius: 50%;
          min-width: 50px;
          display: flex;
          align-items: center;
          justify-content: center;
          border: 1px solid #6e6e6e;
          &.active {
            background-color: #009640;
            border-color: #009640;
          }
          .svg-order-cancelled {
            position: relative;
            right: -3px;
          }
        }
        .status {
          color: #707070;
        }
        .date {
          color: $orange;
        }
        &.active {
          .status {
            color: #000;
          }
          figure {
            border-color: #009640;
          }
        }
        &:not(:last-child) {
          &:after {
            content: " ";
            position: absolute;
            right: -52%;
            top: 50%;
            margin-top: -8px;
            width: 12px;
            height: 16px;
            background: url("../img/icons/order-status-next-arrow.svg") no-repeat center center;
          }
        }
      }
    }
    .products {
      border-top: 1px solid #eeeeee;
      border-bottom: 1px solid #eeeeee;
      padding: 10px 0;
      .product {
        display: flex;
        align-items: center;
        margin-bottom: 9px;
        img {
          width: 130px;
          border: solid 1px #f2f2f2;
          margin-right: 14px;
        }
        .title {
          font-size: 18px;
          font-weight: normal;
        }
        .description {
          color: #909090;
          font-weight: 300;
        }
        .props {
          color: #909090;
          font-weight: 300;
          margin-bottom: 5px;
          .text-dark {
            color: #000 !important;
          }
        }
        .prices {
          font-size: 18px;
          font-weight: normal;
          .old {
            text-decoration: line-through;
            font-weight: 300;
            color: #666666;
            margin-right: 4px;
          }
        }
      }
    }
    .actions {
      .btn {
        width: 47%;
        height: 40px;
        padding: 3px 5px 3px 8px;
        font-size: 13px;
        i {
          margin-left: 5px;
        }
      }
    }
  }
  .info-box {
    background-color: #fff;
    padding: 9px;
    margin-bottom: 9px;
    h4 {
      font-size: 18px;
      color: #5e5e5e;
      font-weight: normal;
      margin-bottom: 20px;
    }
    p {
      font-size: 14px;
      line-height: 22px;
      color: #000;
      font-weight: 300;
      margin-bottom: 24px;
    }
    .links {
      a {
        color: #000;
        text-decoration: underline;
        font-weight: normal;
      }
    }
  }
  .form-title {
    font-size: 14px;
    text-transform: uppercase;
    font-weight: normal;
    margin-bottom: 20px;
  }
  .product {
    display: flex;
    align-items: center;
    img {
      width: 130px;
      border: solid 1px #f2f2f2;
      margin-right: 14px;
    }
    .title {
      font-size: 18px;
      font-weight: 500;
    }
    .description {
      color: #909090;
      font-weight: 300;
    }
    .props {
      color: #909090;
      font-weight: 300;
      margin-bottom: 5px;
      .text-dark {
        color: #000 !important;
      }
    }
    .prices {
      font-size: 18px;
      font-weight: normal;
      .old {
        text-decoration: line-through;
        font-weight: 300;
        color: #666666;
        margin-right: 4px;
      }
    }
  }
  .reason-holder {
    padding: 15px 0 0 30px;
    margin-bottom: 20px;
  }
  .select-shipping-company {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 15px;
    .flo-radio {
      display: flex;
      align-items: center;
      margin-bottom: 0;
      border: solid 1px #f2f2f2;
      height: 90px;
      padding: 8px;
      width: 48%;
      figure {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0;
        img {
          width: 110px;
        }
      }
      i {
        margin-right: 10px;
      }
    }
  }
  hr {
    border-color: #f2f2f2;
    margin: 20px 0;
  }
  .rebate-completed-alert {
    text-align: center;
    font-size: 14px;
    .code {
      font-size: 18px;
    }
  }
  .rebate-process {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    @include list-unstyled;
    .process-box {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      margin-bottom: 15px;
      i {
        margin-right: 10px;
        min-width: 39px;
      }
      div {
        font-size: 14px;
        line-height: 16px;
        color: #909090;
        font-weight: 300;
        text-align: left;
      }
    }
  }
  .newsletter-choice {
    position: relative;
    .flo-checkbox {
      color: $orange;
      font-size: 18px;
      span {
        margin-right: 10px;
        top: 3px;
      }
    }
    .error.help-block {
      color: $red;
      font-weight: normal;
      position: absolute;
      bottom: -10px;
      left: 29px;
    }
    p {
      font-size: 14px;
      font-weight: 300;
      position: relative;
      top: -4px;
      margin-left: 30px;
      &.info-text {
        top: -18px;
      }
    }
  }
  .coupon-box {
    margin-bottom: 12px;
    .title {
      background-color: #000;
      padding: 7px 12px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      h5 {
        color: #fff;
        font-size: 16px;
        font-weight: normal;
        margin: 0;
      }
      i {
        font-size: 18px;
      }
    }
    .body {
      display: flex;
      align-items: center;
      border: 1px solid #dbdbdb;
      border-top: none;
      position: relative;
      .details {
        padding: 12px;
        border-right: 1px dashed #dbdbdb;
        font-size: 14px;
        line-height: 30px;
        width: 55%;
      }
      .price-holder {
        display: flex;
        width: 45%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .price {
          font-size: 50px;
          font-weight: 500;
          line-height: 60px;
          color: $orange;
          margin-bottom: 15px;
        }
        .btn {
          border-radius: 8px;
          font-size: 17px;
          padding: 10px 7px;
        }
      }
    }
    &.passive {
      .title {
        background-color: #c2c2c2;
      }
      .price-holder {
        .price {
          color: #d1d1d1;
        }
        .used {
          font-size: 17px;
        }
      }
    }
  }
  .more-coupons {
    font-size: 12px;
    color: #c2c2c2;
    text-decoration: underline;
  }
  .address-box {
    margin-bottom: 15px;
    font-weight: normal;
    .title {
      color: $orange;
      display: flex;
      font-size: 16px;
      align-items: center;
      margin-bottom: 5px;
    }
    .details {
      font-size: 14px;
      border: 1px solid #dbdbdb;
      padding: 15px;
      height: 100px;
      margin-bottom: 10px;
      color: #000;
    }
    .actions {
      padding-left: 15px;
      a {
        font-size: 14px;
        text-decoration: underline;
        color: #000;
        margin-right: 30px;
      }
    }
  }
  .contact-form {
    textarea.form-control {
      padding: .5rem .55rem;
      & + .error {
        color: $red;
      }
    }
  }
}