.cart-page-wrapper {
  background-color: #eeeeee;
  padding: 10px;
  min-height: 100vh;
  &.empty-cart {
    padding: 0;
    min-height: auto;
  }
  .steps {
    @include list-unstyled;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
    margin-top: 4px;
    position: relative;
    li {
      width: 33.33333%;
      a {
        line-height: 21px;
        text-align: center;
        font-size: 14px;
        color: #000;
        text-transform: uppercase;
        display: block;
        position: relative;
        &:before {
          content: " ";
          @include size(18px);
          border-radius: 50%;
          display: table;
          margin: auto;
          background-color: #eeeeee;
          border: 4px solid #969696;
          position: relative;
          z-index: 3;
        }
      }
      &.active {
        a {
          &:before {
            border-color: $green;
            background-color: #fff;
          }
          &:after {
            content: " ";
            @include size(4px);
            border-radius: 50%;
            background-color: $green;
            position: absolute;
            z-index: 3;
            left: 50%;
            margin-left: -2px;
            top: 7px;
          }
        }
      }
      &.completed {
        a {
          position: relative;
          &:before {
            background-color: $green;
            border-color: $green;
            z-index: 4;
          }
          &:after {
            content: " ";
            background: url(../img/icons/check.svg) no-repeat 50%;
            display: inline-block;
            position: absolute;
            z-index: 4;
            width: 18px;
            height: 18px;
            left: 50%;
            margin-left: -10px;
            top: 0;
            transform: scale(.8);
          }
        }
        &:after {
          content: " ";
          position: absolute;
          width: 33%;
          border-bottom: 4px solid $green;
          top: 7px;
          right: 50%;
          z-index: 3;
        }
        &:nth-of-type(2) {
          &:after {
            right: 19%;
          }
        }
      }
    }
    &:before {
      content: " ";
      position: absolute;
      left: calc(33% / 2);
      right: calc(33% / 2);
      top: 7px;
      z-index: 2;
      height: 4px;
      background: url("../img/dot.svg") repeat-x center center;
      opacity: .5;
    }
  }
  .product-list {
    .product {
      background-color: #fff;
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      position: relative;
      padding: 5px 0 5px 5px;
      .remove {
        position: absolute;
        right: 10px;
        top: 10px;
        color: #8b8b8b;
        font-size: 16px;
        line-height: 16px;
      }
      figure {
        margin: 0 15px 0 0;
        img {
          width: 120px;
          height: auto;
        }
      }
      .text-holder {
        h3 {
          font-size: 14px;
          margin: 0 0 3px;
        }
        .info {
          font-size: 12px;
          display: block;
        }
        .count {
          display: flex;
          align-items: center;
          font-size: 12px;
          span {
            margin-right: 8px;
          }
          select {
            border: 1px solid #888888;
            color: #888888;
            height: 23px;
            border-radius: 0;
            background: url("../img/icons/select-arrow-down.svg") transparent no-repeat center right 5px;
            font-size: 12px;
            padding: 2px 10px 2px 5px;
            width: 48px;
            *width: 48px;
            *background: #fff;
            -webkit-appearance: none;
          }
        }
        .price {
          color: $orange;
          position: absolute;
          right: 15px;
          bottom: 10px;
          font-weight: 500;
        }
        .old-price {
          color: #d3d3d3;
          font-size: 11px;
          position: absolute;
          text-decoration: line-through;
          right: 15px;
          bottom: 30px;
          font-weight: 500;
        }
      }
    }
    &.ordered {
      .product {
        padding: 0;
        figure {
          img {
            width: 100px;
          }
        }
      }
    }
  }
  .campaign-form {
    margin-bottom: 10px;
    .card {
      border: none;
      border-radius: 0;
      .btn-link {
        font-size: 13px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        color: $orange;
        i {
          color: #000;
          font-size: 20px;
          transition: all ease .3s;
        }
        &.active {
          i {
            transform: rotate(180deg);
          }
        }
      }
      .card-body {
        padding: 10px 10px 20px;
        .form-group {
          display: flex;
          align-items: center;
          justify-content: space-between;
          .form-control {
            height: 32px;
            line-height: 32px;
            font-size: 11px;
            border-color: #c4c4c4;
            border-radius: 0;
            margin-right: 7px;
            &::-webkit-input-placeholder {
              color: #000;
            }
            &::-moz-placeholder {
              color: #000;
            }
            &:-ms-input-placeholder {
              color: #000;
            }
            &:-moz-placeholder {
              color: #000;
            }
          }
          button {
            height: 32px;
            font-size: 12px;
            color: #fff;
          }
        }
        .saved-coupons {
          font-size: 12px;
          color: #776a6a;
          text-decoration: underline;
        }
      }
    }
  }
  .cart-summary {
    padding: 10px 15px 15px;
    background-color: #fff;
    margin-bottom: 20px;
    table {
      width: 100%;
      font-size: 13px;
      tr {
        td {
          padding: 5px 0;
        }
        td:first-child {
          width: 45%;
          &:after {
            content: ":";
            float: right;
          }
        }
        td:last-child {
          text-align: right;
        }
        &.total {
          font-size: 14px;
          font-weight: 500;
        }
      }
    }
    .continue-to-shopping-link {
      font-size: 12px;
      color: #000;
      text-decoration: underline;
      display: table;
      margin: 30px auto;
    }
  }
  .payment-type-tabs {
    .nav-tabs {
      li {
        margin-right: 5px;
        a {
          color: #929292;
          text-transform: uppercase;
          font-size: 12px;
          border: 0;
          border-bottom: 3px solid #929292;
          background-color: #fff;
          border-radius: 0;
          &.active {
            background-color: $orange;
            color: #fff;
            border-color: #000;
          }
        }
      }
    }
    .tab-content {
      background-color: #fff;
      padding: 15px;
      color: #827d7d;
      font-size: 12px;
      label {
        color: #827d7d;
        font-size: 12px;
        &.error {
          color: $red;
        }
      }
      .form-control {
        font-size: 12px;
        border: 1px solid #888888;
        color: #827d7d;
        border-radius: 0;
        padding: 2px 10px;
        height: 32px;
        &.error {
          border-color: $red;
        }
      }
      select {
        border: 1px solid #888888;
        color: #827d7d;
        border-radius: 0;
        background: url("../img/icons/select-arrow-down.svg") transparent no-repeat center right 10px;
        padding: 2px 10px;
        height: 32px;
        *background: #fff;
        -webkit-appearance: none;
      }
      .card-holder {
        position: relative;
        margin-top: 40px;
      }
      .card-logo {
        position: absolute;
        right: 0;
        top: -10px;
      }
      .cvv-warning {
        font-size: 11px;
        color: #827d7d;
        font-style: italic;
      }
      .flo-checkbox {
        span {
          border-width: 2px;
          min-width: 20px;
          i {
            left: 0;
            top: 0;
          }
        }
      }
      .checkbox-warning {
        font-size: 11px;
      }
    }
  }
  .confirm-cart-button {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    font-size: 18px;
    width: 100%;
    z-index: 99;
    padding: 15px 0;
    i {
      margin-left: 10px;
      font-size: 22px;
    }
  }
  .order-complete {
    padding: 20px;
    background-color: #fff;
    margin-bottom: 5px;
    p {
      margin-bottom: 10px;
      font-size: 16px;
      &.small {
        font-size: 14px;
      }
    }
  }
  table.order-summary {
    width: 90%;
    display: table;
    margin: auto;
    tr:not(:last-child) {
      border-bottom: 1px solid #eeeeee;
    }
    td {
      padding: 8px;
    }
  }
  .user-shopping-types {
    padding: 15px;
    background-color: #fff;
    label.error {
      color: red;
    }
    .flo-radio {
      text-transform: uppercase;
      margin-bottom: 15px;
      i {
        margin-right: 8px;
      }
    }
    .forgot-password {
      font-size: 12px;
      color: $body-color;
      float: right;
      text-align: right;
      text-decoration: underline;
    }
  }
  .password-reset-form {
    display: none;
    p {
      font-size: 12px;
    }
    .btn-warning {
      border-radius: 4px;
      color: #fff;
      margin-bottom: 15px;
    }
    .form-group {
      margin-bottom: 30px;
    }
  }
  .cart-login-form {
    display: none;
    &.active {
      display: block;
    }
    .btn-warning {
      color: #fff;
      margin-bottom: 15px;
    }
    .facebook-login-button {
      background-color: #415e9b;
      border-color: #415e9b;
      display: flex;
      align-items: center;
      padding: 0 25px;
      line-height: 48px;
      i {
        margin-right: 25px;
        line-height: 48px;
        padding-right: 25px;
        border-right: 1px solid #fff;
      }
    }
    .gender-choice {
      display: flex;
      align-items: center;
      .title {
        color: #827d7d;
        font-size: 12px;
        font-style: italic;
        margin-right: 20px;
      }
      label {
        margin-bottom: 0;
        margin-right: 20px;
        color: #827d7d;
        text-transform: none;
        input:checked {
          & + i + span {
            color: #827d7d;
          }
        }
      }
    }
    .checkboxes {
      label {
        display: block;
        color: #827d7d;
        font-size: 12px;
        margin-bottom: 20px;
        span {
          min-width: 20px;
          border-width: 2px;
          float: left;
          i {
            top: 0;
          }
        }
        input {
          display: block;
          visibility: hidden;
          position: absolute;
        }
        input:checked + span {
          color: inherit;
        }
        .text-holder {
          padding-left: 30px;
          a {
            color: #000;
            text-decoration: underline;
          }
        }
        & + .error {
          color: #ff0000;
          font-size: 12px;
          padding-left: 30px;
          margin-top: -15px;
          display: block;
          margin-bottom: 15px;
        }
      }
    }
  }

  .shipping-type-tabs {
    padding-bottom: 50px;
    .nav-tabs {
      li {
        margin-right: 5px;
        a {
          color: #929292;
          text-transform: uppercase;
          font-size: 12px;
          border: 0;
          border-bottom: 3px solid #929292;
          background-color: #fff;
          border-radius: 0;
          &.active {
            background-color: $orange;
            color: #fff;
            border-color: #000;
          }
        }
      }
    }
    .tab-content {
      .tab-card {
        background-color: #fff;
        padding: 15px;
        color: #827d7d;
        font-size: 12px;
        margin-bottom: 10px;
        .section-title {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-bottom: 5px;
          h4 {
            font-size: 14px;
            font-weight: normal;
            margin: 0;
            color: $orange;
          }
          a {
            font-size: 12px;
            text-decoration: underline;
          }
        }
        .address-card {
          border: 1px solid #aeaeae;
          padding: 15px;
          margin-bottom: 10px;
          label {
            margin-bottom: 0;
          }
          .text-holder {
            padding-left: 30px;
          }
          h5 {
            font-size: 13px;
            font-weight: 500;
            margin-bottom: 5px;
          }
          p {
            margin-bottom: 5px;
          }
          a.edit-address {
            color: $body-color;
            text-decoration: underline;
          }
        }
        .other-addresses {
          .btn-link {
            padding: 0 10px 0 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: #000;
            transition: all ease .3s;
            span {
              font-size: 12px;
              text-decoration: underline;
            }
            i {
              font-size: 24px;
              transition: all ease .3s;
            }
            &.active {
              i {
                transform: rotate(180deg);
                transition: all ease .3s;
              }
            }
          }
        }
        .corporate-infos {
          display: none;
          .form-group {
            margin-bottom: 10px;
          }
        }
        .delivery-choice {
          h4 {
            font-size: 14px;
            color: #000;
            font-weight: normal;
          }
          .holder {
            border: 1px solid #aeaeae;
            padding: 15px 10%;
            .companies {
              display: flex;
              align-items: center;
              justify-content: space-between;
            }
            .flo-radio {
              display: flex;
              align-items: center;
            }
            .warning {
              font-size: 12px;
              color: #000;
              text-align: center;
            }
          }
        }
        .gift-infos {
          display: none;
          .form-group {
            margin-bottom: 10px;
            textarea {
              font-style: italic;
              height: auto;
            }
          }
        }
      }
      select {
        border: 1px solid #ced4da;
        color: $body-color;
        border-radius: 0;
        background: url("../img/icons/select-arrow-down.svg") transparent no-repeat center right 10px;
        padding: 2px 10px;
        height: 32px;
        *background: #fff;
        -webkit-appearance: none;
      }
      .cvv-warning {
        font-size: 11px;
        color: #827d7d;
        font-style: italic;
      }
      .flo-checkbox {
        span {
          border-width: 2px;
          min-width: 20px;
          i {
            left: 1px;
            top: 1px;
          }
        }
      }
      .checkbox-warning {
        font-size: 11px;
      }
      .billing-addresses {
        display: none;
      }
    }
  }
  .pick-from-shop-info {
    font-size: 14px;
    text-transform: uppercase;
    display: flex;
    align-items: center;
    font-weight: 500;
    margin-bottom: 20px;
    span {
      text-decoration: underline;
    }
    i {
      @include size(20px);
      background-color: $orange;
      color: #fff;
      border-radius: 50%;
      margin-right: 10px;
      text-align: center;
      font-style: normal;
    }
  }
  .pickup-person-infos {
    display: none;
  }
  h5.section-title {
    font-size: 14px;
    font-weight: 500;
  }
  .shop-list {
    display: none;
    .address-card {
      h5.text-warning {
        text-transform: uppercase;
        font-weight: 500 !important;
        color: $orange !important;
        font-size: 14px !important;
      }
    }
    .show-on-map {
      color: #000;
      text-decoration: underline;
      font-size: 14px;
    }
  }
  .empty-cart-alert {
    padding: 30px 0;
    max-width: 385px;
    margin: auto;
    display: block;
    h2 {
      font-size: 30px;
      font-weight: 300;
    }
    p {
      font-size: 14px;
      color: #8e8e8e;
      font-weight: normal;
      a {
        color: #000;
      }
    }
  }
}