.stores-page-wrapper {
  .store-search-form {
    position: relative;
    margin-bottom: 1rem;
    .form-control {
      text-align: center;
      &::placeholder {
        color: rgb(130,125,125);
        opacity: 1;
      }

      &:-ms-input-placeholder {
        color: rgb(130,125,125);
      }

      &::-ms-input-placeholder {
        color: rgb(130,125,125);
      }
    }
    i.fa-search {
      position: absolute;
      left: 10px;
      top: 7px;
      font-size: 18px;
    }
    .country-holder {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 1rem;
      i {
        min-width: 30px;
        margin-right: 10px;
      }
      .form-control {
        width: 42%;
      }
    }
    .btn-warning {
      background-color: #ff671d;
      border-color: #ff671d;
      position: relative;
      font-size: 14px;
      i {
        position: absolute;
        right: 50px;
        top: 10px;
        font-size: 18px;
      }
    }
  }
  .map-holder {
    margin: 0 -.8rem 15px -.8rem;
    #map {
      height: 190px;
      width: 100%;
    }
  }
  .stores-list {
    .store-box {
      border: 1px solid #dde1e5;
      padding: 10px 30px 10px 40px;
      position: relative;
      color: #000;
      font-size: 13px;
      font-weight: 300;
      height: 55px;
      overflow: hidden;
      transition: all ease .3s;
      cursor: pointer;
      background: url("../img/icons/arrow-down.svg") #e0e0e0 no-repeat top 12px right 10px;
      margin-bottom: 10px;
      &:before {
        content: " ";
        position: absolute;
        left: 13px;
        top: 10px;
        @include size(14px);
        background-color: #c7c7c7;
        border-radius: 50%;
      }
      h4 {
        font-size: 13px;
        font-weight: 500;
        margin-bottom: 0;
      }
      .city {
        color: #5d5d5d;
        margin-bottom: 5px;
      }
      a {
        display: block;
        color: #000;
        opacity: 0;
        visibility: hidden;
        &.get-directions {
          position: absolute;
          bottom: 6px;
          right: 15px;
          display: flex;
          align-items: center;
          i {
            margin-right: 5px;
          }
        }
      }
      &.active {
        background-color: #fff;
        height: auto;
        &:before {
          border: 3px solid $orange;
          background-color: #fff;
        }
        a {
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }
  .stores-summary {
    font-size: 9px;
    text-align: right;
  }
}