.favourites-page-wrapper {
  .product-box {
    .remove-from-favourites-button {
      position: absolute;
      right: 5px;
      top: 5px;
      @include size(23px);
      background: url("../img/icons/remove.svg") no-repeat center center;
      background-size: contain;
      &:hover {
        background: url("../img/icons/remove-orange.svg") no-repeat center center;
        background-size: contain;
      }
    }
  }
}