.product-list-page {
  .title-bar {
    color: #898989;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 12px;
    h1 {
      font-size: 12px;
      font-weight: 300;
      color: $black;
      margin: 0;
    }
  }
  .filter-buttons {
    margin-bottom: 9px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
      height: 36px;
      font-size: 14.5px;
      width: 100%;
      background-color: #fff;
      color: $black;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      text-transform: uppercase;
      i {
        margin-right: 6px;
      }
      &:first-child {
        min-width: calc(50% - 4.5px);
      }
      &:not(:last-child) {
        margin-right: 9px;
      }
      &.change-grid {
        width: 38px;
        min-width: 38px;
        i {
          margin-right: 0;
        }
      }
    }
  }
  .filter-form {
    position: fixed;
    top: 120px;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    z-index: 9;
    opacity: 0;
    visibility: hidden;
    transition: all ease .3s;
    font-weight: 300;
    .form-holder {
      height: calc(100vh - 107px);
      overflow: scroll;
      display: flex;
      flex-direction: column;
    }
    .title-bar {
      background-color: $orange;
      padding: 6px 30px 5px 9px;
      display: flex;
      align-items: center;
      width: 100%;
      min-height: 33px;
      color: #fff;
      float: left;
      font-size: 18px;
      text-transform: uppercase;
      justify-content: flex-start;
      margin-bottom: 0;
    }
    .selected-filters {
      background-color: $orange;
      padding: 0 30px 0 9px;
      display: inline-block;
      width: 100%;
    }
    .selected-filter {
      color: #fff;
      float: left;
      font-size: 14px;
      display: flex;
      align-items: center;
      margin: 4.5px 9px 9px 0;
      .closer {
        font-size: 12px;
        @include size(20px);
        border: 1px solid #fff;
        display: inline-block;
        line-height: 18px;
        text-align: center;
        margin-right: 6px;
      }
    }
    .form-reset-button {
      color: #fff;
      float: left;
      font-size: 14px;
      display: flex;
      align-items: center;
      margin: 4.5px 9px 9px 0;
      .closer {
        font-size: 12px;
        @include size(20px);
        border: 1px solid #fff;
        background-color: #fff;
        display: inline-block;
        line-height: 16px;
        text-align: center;
        margin-right: 5px;
        color: $orange;
      }
    }
    .card {
      border: none;
      border-bottom: 1px solid #ebebeb;
      .btn-link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        padding: 11px 9px;
        text-transform: uppercase;
        i {
          font-size: 18px;
        }
      }
      .card-body {
        padding: 5px 10px 0;
        display: inline-block;
        width: 100%;
        .flo-checkbox {
          font-weight: 500;
          font-size: 14px;
          float: left;
          width: 33%;
          margin-bottom: 9px;
        }
      }
      &.active {
        & > .btn-link {
          color: $orange;
          padding-bottom: 5px;
          i {
            transform: rotate(90deg);
          }
        }
      }
      &.category {
        .card-body {
          padding: 5px 0;
          .sub-category-list {
            @include list-unstyled;
            padding-top: 3px;
            margin: 0;
            label {
              font-size: 14px;
              color: $black;
              font-weight: 300;
              padding: 3px 0;
              display: block;
              input {
                display: none;
                &:checked {
                  & + span {
                    color: $orange;
                    & + span {
                      color: $orange;
                    }
                  }
                }
              }
            }
          }
        }
        .card.sub {
          border-bottom: none;
          .btn-link {
            padding: 5px 10px;
            text-transform: none;
          }
        }
      }
      &.sub {
        .card-body {
          padding: 0 10px 0 15px;
        }
      }
      &.brand {
        .search-holder {
          position: relative;
          margin-bottom: 10px;
          .form-control {
            border-color: #acacac;
            height: 28px;
            border-radius: 0;
            font-size: 13px;
          }
          i {
            position: absolute;
            right: 10px;
            top: 5px;
            font-size: 18px;
          }
        }
        .flo-checkbox {
          width: 100%;
        }
      }
    }
    .size-list {
      @include list-unstyled;
      margin: 0;
      display: inline-block;
      width: 100%;
      padding-right: 15px;
      li {
        width: 12.5%;
        padding-right: 5px;
        float: left;
        .size-checkbox {
          width: 100%;
          input {
            display: none;
            &:checked {
              & + span {
                background-color: $orange;
                color: #fff;
              }
            }
          }
          span {
            border: 1px solid #d2d2d2;
            display: block;
            width: 100%;
            height: 38px;
            line-height: 38px;
            text-align: center;
            font-size: 12px;
            font-weight: 500;
          }
        }
      }
    }
    .submit-button {
      font-size: 18px;
      margin-top: 30px;
      position: fixed;
      bottom: 0;
      border-radius: 0;
      padding: 12px;
      background-color: #a90000;
      border-color: #a90000;
    }
    &.active {
      opacity: 1;
      visibility: visible;
      top: 54px;
    }
    .reset-button {
      position: fixed;
      bottom: 50px;
      width: 96px;
      left: 50%;
      margin-left: -48px;
      z-index: 9;
    }
    .close-filter-form {
      position: absolute;
      right: 9px;
      top: 12px;
      z-index: 13;
    }
    .price-ranges {
      display: flex;
      align-items: center;
      margin-bottom: 18px;
      .form-control {
        border-color: #d2d2d2;
        height: 36px;
        border-radius: 0;
        font-size: 14px;
        width: 80px;
        text-align: center;
        &::placeholder {
          color: #a6a6a6;
          opacity: 1;
        }
        &:-ms-input-placeholder {
          color: #a6a6a6;
        }
        &::-ms-input-placeholder {
          color: #a6a6a6;
        }
      }
      span{
        margin: 0 10px;
        font-size: 22px;
        position: relative;
        top: -2px;
      }
    }
    .price-choices {
      .flo-radio {
        float: left;
        width: 40%;
      }
    }
  }
  .sort-form {
    background-color: $orange;
    position: absolute;
    top: 190px;
    left: 9px;
    right: 9px;
    z-index: 9;
    transition: all ease .3s;
    opacity: 0;
    visibility: hidden;
    .sort-select {
      display: block;
      margin: 10px;
      background-color: #fff;
      border-color: #fff;
      font-size: 14px;
      input {
        display: none;
        &:checked + span {
          border-color: $orange;
          color: $orange;
        }
      }
      span {
        display: block;
        width: 100%;
        font-size: 14px;
        text-transform: uppercase;
        text-align: center;
        padding: 10px;
        color: #000;
      }
    }
    &.active {
      opacity: 1;
      visibility: visible;
      top: 172px;
    }
  }
  .product-grid {
    .row {
      margin-left: -4.5px;
      margin-right: -4.5px;
      .product-wrapper {
        padding-right: 4.5px;
        padding-left: 4.5px;
        flex: 0 0 50%;
        max-width: 50%;
      }
    }
    &.one-column {
      .row {
        .product-wrapper {
          flex: 0 0 100%;
          max-width: 100%;
          .product-box {
            .badge {
              font-size: 14.5px;
              left: 6px;
              top: 6px;
              width: 102px;
              padding: 5px;
            }
            .add-to-favourites-button {
              transform: scale(1.4);
              right: 15px;
              top: 15px;
            }
            .title {
              font-size: 23px;
            }
            .description {
              font-size: 18px;
            }
            .prices {
              margin: 0;
              padding: 15px 0;
              .old-price, .price {
                font-size: 18px;
              }
            }
            .discount {
              padding: 10px 0;
              margin: 0;
              font-size: 23px;
              line-height: 23px;
              display: block;
            }
          }
        }
      }
    }
  }
}

.pagination-wrapper {
  background-color: #fff;
  padding: 5px 9px;
}
.pagination {
  font-size: 16px;
  margin-bottom: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  li {
    a {
      display: flex;
      align-items: center;
      justify-content: center;
      @include size(37px);
    }
    &.page-item {
      &:first-child, &:last-child {
        .page-link {
          border-radius: 0;
        }
      }
    }
  }
}

body {
  &.sort-form-active {
    .product-list-page:before {
      content: " ";
      background: rgba(0,0,0,.6);
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 8;
    }
    .sort-button {
      background-color: $orange;
      color: #fff;
      position: relative;
      z-index: 10;
    }
    .sort-form {
      z-index: 10;
    }
  }
  &.nav-up, &.nav-down {
    .breadcrumbs {
      position: fixed;
      transition: all ease .3s;
      left: 0;
      right: 0;
      top: -50px;
      z-index: 2;
      margin: 0;
    }
    .filter-buttons {
      position: fixed;
      left: 0;
      right: 0;
      top: -50px;
      transition: all ease .3s;
      z-index: 2;
      background-color: #ebebeb;
      border-bottom: 1px solid #ebebeb;
      a:first-child {
        min-width: calc(50% - 1px);
      }
      a:not(:last-child) {
        margin-right: 1px;
      }
    }
  }
  &.nav-up {
    .breadcrumbs {
      transition: none;
      opacity: 0;
    }
  }
  &.nav-down {
    .site-header {
      z-index: 10;
    }
    .breadcrumbs {
      top: 54px;
      border-top: 1px solid #ebebeb;
      border-bottom: 1px solid #ebebeb;
      z-index: 10;
    }
    .filter-buttons {
      top: 89px;
      z-index: 10;
    }
    .title-bar {
      z-index: 10;
    }
    .product-list-page .sort-form {
      top: 125px;
      position: fixed;
    }
  }
  &.top-reached {
    .breadcrumbs {
      position: initial;
      margin: 10px 0;
      border: none;
    }
    .filter-buttons {
      position: initial;
      border: none;
    }
  }
  &.block-scroll {
    position: fixed;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
  }
}