.form-group {
  &.animated {
    position: relative;
    label {
      position: absolute;
      color: #827d7d;
      font-size: 13px;
      background-color: #fff;
      top: 8px;
      left: 5px;
      padding: 0 5px;
      transition: all ease .3s;
    }
    .form-control {
      color: $black;
      padding: 0.5rem 0.55rem;
    }
    .error {
      position: absolute;
      color: #ff0000;
      left: auto;
      right: 9px;
      top: auto;
      bottom: 10px;
      font-style: italic;
      font-size: 11px;
    }
    &.active {
      label {
        top: -8px;
        font-size: 11px;
      }
    }
    &.has-error {
      label {
        color: #ff0000;
      }
    }
    &.password-input {
      .error {
        right: 55px;
      }
    }
  }
  &.has-success {
    &:after {
      font-family: "Font Awesome 5 Free"; font-weight: 900; content: "\f00c";
      display: inline-block;
      font-style: normal;
      font-variant: normal;
      text-rendering: auto;
      -webkit-font-smoothing: antialiased;
      position: absolute;
      right: 10px;
      bottom: 7px;
      color: $green;
    }
    &.selectbox, &.password-input {
      &:after {
        display: none;
      }
    }
  }
  &.phone-input, &.address-input {
    position: relative;
    label {
      position: absolute;
      color: #827d7d;
      background-color: #fff;
      top: -9px;
      font-size: 11px !important;
      left: 5px;
      padding: 0 5px;
      transition: all ease .3s;
    }
    .error {
      position: absolute;
      color: #ff0000;
      left: auto;
      right: 9px;
      top: auto;
      bottom: 8px;
      font-style: italic;
      font-size: 11px;
    }
    .form-control {
      height: 35px;
    }
  }
  &.address-input {
    textarea {
      height: auto !important;
      padding: 12px 9px;
    }
  }
  &.has-error {
    label {
      color: #ff0000;
    }
    .form-control {
      color: #ff0000 !important;
      &::-webkit-input-placeholder {
        color: #ff0000;
      }
      &::-moz-placeholder {
        color: #ff0000;
      }
      &:-ms-input-placeholder {
        color: #ff0000;
      }
      &:-moz-placeholder {
        color: #ff0000;
      }
    }
    select + .error {
      right: 37px;
    }
  }
  &.password-input {
    a {
      position: absolute;
      right: 10px;
      top: 8px;
      color: #827d7d;
      font-size: 12px;
    }
  }
  &.no-validation-check {
    &.has-success:after {
      display: none;
    }
  }
}