.home-page-wrapper {
  background-color: #fff;
  .slogans-bar {
    background-color: #f8f8f8;
    .icons-holder {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 9px;
      .icon-box {
        display: flex;
        align-items: center;
        position: relative;
        i {
          margin-right: 7px;
        }
        h4 {
          font-size: $font-size-base;
          font-weight: 300;
          margin: 0;
          color: #5e5e5e;
        }
      }
    }
  }
  .section-title {
    text-align: center;
    font-size: 24px;
    font-weight: 300;
    margin-bottom: 0;
  }
  .logos-bar {
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow-x: auto;
    margin-right: -10px;
    .logo-box {
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: #fff;
      @include size(80px);
      min-width: 80px;
      border: 1px solid #d2d2d2;
      margin-right: 10px;
      img {
        max-width: 70%;
      }
      &:hover {
        border-color: $orange;
      }
    }
  }
  .categories-bar {
    padding: 15px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    overflow-x: auto;
    margin-right: -10px;
    .category-box {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      position: relative;
      margin-right: 10px;
      figure {
        border-radius: 50%;
        background-color: #fff;
        @include size(80px);
        min-width: 80px;
        border: 1px solid #d2d2d2;
        overflow: hidden;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          width: 100%;
        }
      }
      h4 {
        font-size: 12px;
        font-weight: 300;
        margin: 0;
        color: #5e5e5e;
        text-align: center;
      }
      &:not(:last-child) {
        &:after {
          content: " ";
          background-color: #d2d2d2;
          border-radius: 50%;
          @include size(7px);
          position: absolute;
          right: -32px;
          top: 50%;
          margin-top: -27px;
        }
      }
      &:hover {
        figure {
          border-color: $orange;
        }
        h4 {
          color: $orange;
        }
      }
    }
  }
  .banners {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-right: -10px;
    white-space: nowrap;
    overflow-x: auto;
  }
  .banner-box {
    margin-right: 9px;
    width: 135px;
    min-width: 135px;
    img {
      width: 100%;
      height: auto;
    }
    .title {
      display: block;
      text-align: center;
      color: $body-color;
      font-weight: 300;
      white-space: normal;
      padding: 10px 5px;
    }
  }
  .our-choices {
    background-color: #fffafa;
    padding: 15px 0;
    .product-slider {
      display: flex;
      align-items: flex-start;
      justify-content: flex-start;
      white-space: nowrap;
      overflow-x: auto;
      margin-right: -10px;
      .product-box {
        background-color: #fff;
        -webkit-box-shadow: 2px 3px 0px 0px rgba(181,181,181,0.1);
        -moz-box-shadow: 2px 3px 0px 0px rgba(181,181,181,0.1);
        box-shadow: 2px 3px 0px 0px rgba(181,181,181,0.1);
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        @include size(135px,265px);
        min-width: 135px;
        margin-right: 9px;
        figure {
          position: relative;
          margin-bottom: 5px;
          .badge {
            background-color: #003f67;
            color: #fff;
            font-size: 12px;
            border-radius: 0;
            font-weight: 300;
            text-transform: uppercase;
            position: absolute;
            left: 3px;
            top: 3px;
          }
          .add-to-favourites-button {
            position: absolute;
            right: 3px;
            top: 3px;
          }
        }
        img {
          width: 100%;
          height: auto;
        }
        .title {
          font-size: 14px;
          color: #000;
          text-align: center;
          font-weight: 500;
          margin-bottom: 3px;
          white-space: normal;
        }
        .description {
          color: $black;
          white-space: normal;
          overflow: hidden;
          text-overflow: ellipsis;
          -webkit-line-clamp: 2;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          line-height: 14px;
          height: 30px;
          padding: 0 5px;
          text-align: center;
        }
        .prices {
          font-weight: 300;
          margin-top: 20px;
          width: 100%;
          text-align: center;
          .old-price {
            color: #a5a5a5;
            font-size: 14px;
            text-decoration: line-through;
          }
          .price {
            font-size: 14px;
            font-weight: 500;
          }
          &.discounted {
            margin: 5px 0;
            border-bottom: 1px solid #efefef;
            padding-bottom: 5px;
            .old-price {
              font-size: 12px;
            }
            .price {
              font-size: 12px;
              color: $black;
            }
          }
        }
        .discount {
          color: $black;
          text-align: center;
          font-size: 14px;
          line-height: 16px;
          .text-warning {
            display: block;
            font-weight: 500;
          }
        }
        &:focus {
          outline: none;
        }
      }
    }
  }
  .blogger {
    padding: 15px 0;
    .blog-box {
      padding: 0 5px;
      display: inline-block;
      width: 100%;
      h5 {
        display: block;
        text-align: center;
        color: $body-color;
        font-size: 12px;
        font-weight: 300;
        padding: 15px;
      }
    }
    .btn-dark {
      font-size: 12px;
      padding: 3px 15px;
      border-radius: 0;
      font-weight: 300;
    }
  }
}