.mobile-menu {
  .close-menu {
    position: absolute;
    right: 18px;
    top: 18px;
  }

  .mm-navbars_top {
    border-bottom: none;
  }

  .mm-navbar {
    padding: 15px 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #f8f8f8;
    border-bottom: none;
    height: 52px;

    .mm-btn_prev {
      position: initial;
      width: 42px;

      &:not(.mm-hidden) + .mm-navbar__title {
        color: $orange;
      }
    }

    .mm-navbar__title {
      padding: 0;
      font-size: 22px;
      line-height: 28px;
      color: #5e5e5e;
      text-align: left;
      font-weight: normal;
      text-transform: uppercase;
    }
  }

  .mm-panels {
    background-color: #fff;

    .mm-navbar {
      display: none;
    }

    .mm-panel {
      background-color: #fff;
      padding-top: 19.5px;
    }
  }

  .mm-listview {
    .mm-btn_next:after {
      border-color: #5e5e5e;
      width: 12px;
      height: 12px;
    }

    .mm-listitem {
      border-bottom-color: #fff;

      > a {
        font-size: 16px;
        text-transform: uppercase;
        padding-top: 7.5px;
        padding-bottom: 7.5px;

        &.mm-listitem__text:not(.mm-listitem__btn) {
          padding: 14px 20px;

          &:after {
            content: "";
            display: block;
            position: absolute;
            bottom: 16px;
            width: 10px;
            height: 10px;
            border: 2px solid #5e5e5e;
            transform: rotate(135deg);
            right: 23px;
            left: auto;
            border-bottom: none;
            border-right: none;
            box-sizing: content-box;
            margin: auto;
          }
        }
      }

      img {
        border: 1px solid #ebebeb;
        margin-right: 20px;
      }
    }
  }

}

.mm-btn_close {
  background: url("../img/icons/close.svg") no-repeat center center;
  margin-right: 5px;

  &:after, &:before {
    display: none;
  }
}

.mm-btn_close:after, .mm-btn_close:before {
  display: none;
}

.mm-btn_next:after, .mm-btn_prev:before {
  width: 12px;
  height: 12px;
  border-color: #5e5e5e;
}