.flo-radio {
  display: block;
  input[type=radio] {
    opacity: 0;
    visibility: hidden;
    position: absolute;
    &:checked {
      & + i {
        background: url("../img/dot-orange.svg") no-repeat center center;
        &:before {
          @include size(10px);
          background: $orange;
          border-radius: 50%;
          position: absolute;
          left: 50%;
          top: 50%;
        }
      }
      & + i + span {
        color: $orange;
      }
    }
  }
  i {
    @include size(20px);
    border: 1px solid #000;
    border-radius: 50%;
    margin-right: 5px;
    position: relative;
    float: left;
  }
  &.active {
    color: $orange;
  }
}