/*!
 * Primary color settings
 */
$font-family-base: 'Oswald';

$grid-gutter-width: 18px !default;
$spacer: 0.8rem !default;

$body-color: #5e5e5e;
$body-bg: #F8F8F9;

$font-size-base: 0.8125rem !default;
$font-weight-base: 300;

$orange: #ff671d;
$green: #28a745;
$red: #ff0000;
$warning: $orange !default;
$danger: $red !default;

$link-color: $orange !default;
$link-hover-color: darken($link-color, 0%) !default;
$link-hover-decoration: none !default;

%svg-common {
  background-image: url("/assets/img/iconset/view/svg/sprite.view.svg");
}

[class|="svg"] {
  display: inline-block;
}

$container-max-widths: (
        sm: 540px,
        md: 720px,
        lg: 960px,
        xl: 1170px
) !default;


$input-focus-box-shadow: none;
$input-placeholder-color: #a5a5a5;
$input-focus-border-color: $orange;
$input-border-radius: 0;

$btn-border-radius: 0;
$btn-border-radius-lg: 0;

$ipadlandscape: 1024px;
$ipadportrait: 768px;


$pagination-border-color: #9d9d9d;
$pagination-color: #000;
$pagination-padding-y: 8px;
$pagination-padding-x: 12px;

$pagination-active-bg: #e7893f;
$pagination-active-border-color: #e7893f;


// Tooltip
$tooltip-font-size:                 12px!default;
$tooltip-max-width:                 200px !default;
$tooltip-color:                     #000 !default;
$tooltip-bg:                        #dcdcdc !default;
$tooltip-border-radius:             4px !default;
$tooltip-opacity:                   .9 !default;
$tooltip-padding-y:                 .25rem !default;
$tooltip-padding-x:                 .5rem !default;
$tooltip-margin:                    0 !default;

$tooltip-arrow-width:               .8rem !default;
$tooltip-arrow-height:              .4rem !default;
$tooltip-arrow-color:               #dcdcdc !default;


button:focus {
  outline: none !important;
}

.noty_bar {
  background-color: $orange;
  color: #fff;
  padding: 10px 15px;
  .noty_body {
    font-size: 16px;
    font-weight: 300;
  }
}

.row {
  &.small-gutter {
    margin-left: -4.5px;
    margin-right: -4.5px;
    & > div[class^="col"] {
      padding: 0 4.5px;
    }
  }
}

.modal {
  .modal-content {
    border-radius: 0;
  }
  .modal-header {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    .close {
      background: url("../img/icons/close.svg") no-repeat center center;
      margin: -4px -8px -1rem auto;
      opacity: 1;
    }
  }
  .modal-body {
    label.error {
      color: red;
      margin-bottom: 0;
    }
    .form-control.error {
      border-color: red;
    }
  }
}

.custom-select {
  background: url(../img/icons/select-arrow-down.svg) transparent no-repeat center right 8px !important;
  *background: #fff !important;
  -webkit-appearance: none;
  border-radius: 0 !important;
}