.faq-page-wrapper {
  .search-box {
    background: url("../img/search-box-bg.jpg") no-repeat center center;
    background-size: cover;
    padding: 15px 9px;
    margin-bottom: 15px;
    h2 {
      text-align: center;
      font-size: 18px;
      font-weight: normal;
      margin-bottom: 15px;
    }
    form {
      position: relative;
      .form-control {
        border-radius: 0;
        height: 40px;
        line-height: 40px;
      }
      button {
        position: absolute;
        min-width: 40px;
        @include size(40px);
        padding: 0;
        background-color: $orange;
        border: none;
        right: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
  .category-box {
    background-color: #fff;
    padding: 9px;
    .main-container {
      display: none;
    }
    .question-categories {
      @include list-unstyled;
      margin-bottom: 15px;
      li {
        &:not(:last-child) {
          margin-bottom: 9px;
        }
        a {
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 16px;
          font-weight: normal;
          color: rgb(94, 94, 94);
          padding: 10px 8px 10px 10px;
          border: 1px solid rgb(208,208,208);
        }
      }
    }
    .go-back {
      display: flex;
      align-items: center;
      font-size: 18px;
      font-weight: normal;
      margin-bottom: 12px;
      i {
        margin-right: 16px;
      }
    }
    .card {
      border: 1px solid #d0d0d0;
      border-radius: 0;
      margin-bottom: 9px;
      .btn-link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        text-align: left;
        background-color: #f1f1f1;
        font-size: 14px;
        font-weight: normal;
        border-radius: 0;
        padding: 9px;
        border-bottom: 1px solid #f1f1f1;
      }
      &.active {
        .btn-link {
          border-bottom: 1px solid #d0d0d0;
          color: $orange;
          i {
            transform: rotate(90deg);
          }
        }
      }
      .card-body {
        padding: 9px;
      }
    }
  }
}